@mixin resetsComponent {

    .margin-none {
        margin: 0; }


    .padding-none {
        padding: 0; }


    .border-none {
        border: none; }


    .background-none {
        background: none; }


    .outline-none {
        outline: 0; }


    .shadow-none {
        box-shadow: none; }


    .width-auto {
        width: auto; }


    .color-inherit {
        color: inherit; } }
